/* MoodContentのまとまりに対して */
.Top {
  /* text-align: center; */
  margin-right: 500px;
  margin-left: 50px;
  /* padding-right: 500px; */
  /* width: 500px; */
}

.PhoneTop {
  position: absolute;
  left: 30px;
  right: 0;
  margin-right: 1px;
  margin-left: 1px;
  margin-top: 550px;
}



.pie-chart-parent {
  display: inline-block;
  vertical-align:top;
  position: fixed;
  right: 90px;
  background-color: #F5F5F5;
  border-radius: 15px;
  /* margin-right: 20px; */
  /* width: 25%; */
}

.PhonePieChartParent {
  position: absolute;
  right: 90px;
  left: 30px;
  top: 200px;
  width: 420px;
  background-color: #F5F5F5;
  border-radius: 15px;
}

.pie-chart-child {
  padding: 12px;

}

.label {
  font-size: 20px;
  display: inline-block;
  vertical-align: middle;
  margin-left: 25px;
  margin-top: 30px;
}

.label-color1, .label-color2, .label-color3, .label-color4 {
  width: 30px;
  height: 15px;
  display: inline-block;
  vertical-align: middle;
  padding-left: 0;
  margin-left: 5px;
  margin-top: 19px;

}

.label-color1 {
  background-color: #FFCC33;
}
.label-color2 {
  background-color: #98FB98;
}
.label-color3 {
  background-color: #DDA0DD;
}
.label-color4 {
  background-color: #D3D3D3;
}

.common {
  background-color: #F5F5F5;
  width: 80%;
  height: auto;
  /* margin: 100px 300px 100px 300px; */
  border-radius:15px;
  position: absolute;
  right: 170px;
  font-family: "SF Pro Text","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
}

.PhoneCommon {
  background-color: #F5F5F5;
  width: 80%;
  height: auto;
  /* margin: 100px 300px 100px 300px; */
  border-radius:15px;
  position: absolute;
  right: 50px;
}

.date-common {
  margin: 12px auto 40px 12px;
  display:-webkit-flex;
  display:flex;
}

.mood-common {
  display:-webkit-flex;
  display:flex;
  margin: 12px auto 40px 12px;
}

.note-common {
  /* display:flex; */
  margin-left: 12px;

}

.calendar-icon {
  width: 25px;
  height: 25px;
  display:inline-block;
  vertical-align:top;
  margin: 12px auto 12px 12px;
}

.mood-icon {
  width: 25px;
  height: 25px;
  display:inline-block;
  vertical-align:top;
  margin: 12px auto 12px 12px;
}

.note-icon {
  width: 30px;
  height: 30px;
  display:inline-block;
  vertical-align:top;
  margin: 12px auto 12px 12px;
}

.detail-date {
  font-size: 25px;
  font-weight: 500;
  /* padding-right: 90%; */
  display:inline-block;
  vertical-align:top;
  /* margin: 12px auto 12px 12px; */
  padding-right: 1100px;
}

.detail-mood {
  font-size: 25px;
  font-weight: 500;
  /* padding-right: 90%; */
  display:inline-block;
  vertical-align:top;
  margin: 12px auto 12px 12px;
  padding-right: 1100px;
}

.detail-note {
  font-size: 20px;
  font-weight: 500;
  padding-left: 1px;
  /* display:inline-block; */
  /* vertical-align:top; */
  margin: 12px 30px 12px 12px;
  /* padding-right: 120px; */
}

